.breadcrumbs {
  background-color: #ccc;
  ul {
    li {
      float: left;
      display: block;
      margin: 13px 0;
      a {
        color: #000;
        text-decoration: none;
        &:hover {
          text-decoration: none; } }
      .fa {
        margin: 0 5px;
        color: $activ_col; } } } }
.article-content,
aside {
  margin: 20px 0;
  h2 {
    font-size: 24px;
    text-align: center; }
  img {
    margin: 20px auto; }
  p {
    line-height: 24px;
    margin: 10px 0; } }
.related-posts,
.reltours {
  margin: 100px 0; }
.reltours {
  h3 {
    text-align: center;
    color: $btm_col;
    text-transform: uppercase;
    font-size: 22px;
    margin: 30px 0;
    text-shadow: 1px 0 1px rgba(0,0,0,.5); }
  .item {
    border-top: 3px solid $btm_col;
    p {
      text-align: center;
      margin: 10px 0;
      @include font('informCTT');
      font-size: 16px; }
    img {
      margin: 15px 0; } }
  .owl-dots {
    text-align: center;
    position: relative;
    bottom: -12px;
    .owl-dot {
      width: 13px;
      height: 13px;
      border-radius: 100%;
      border: 1px solid #ccc;
      background: #fff;
      top: 15px;
      margin-right: 5px;
      display: inline-block;
      &.active {
        background: $btm_col; } } }
  .owl-nav {
    div {
      width: 15px;
      height: 33px;
      background-image: url("images/responceArr.png");
      top: 7px;
      position: absolute;
      &.owl-next {
        right: 0;
        background-position: top right; } } } }
.related-posts {
    .imgHold {
      position: relative;
      overflow: hidden;
      img {
        position: relative;
        -webkit-transition: all 200ms ease-in;
        -webkit-transform: scale(1);
        -ms-transition: all 200ms ease-in;
        -ms-transform: scale(1);
        -moz-transition: all 200ms ease-in;
        -moz-transform: scale(1);
        transition: all 200ms ease-in;
        transform: scale(1);
        z-index: 1;
        &:hover {
          z-index: 2;
          -webkit-transition: all 200ms ease-in;
          -webkit-transform: scale(1.1);
          -ms-transition: all 200ms ease-in;
          -ms-transform: scale(1.1);
          -moz-transition: all 200ms ease-in;
          -moz-transform: scale(1.1);
          transition: all 200ms ease-in;
          transform: scale(1.1); } }
      .capt {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        background: rgba(0,0,0,.5);
        z-index: 10;
        p {
          color: #fff;
          float: left;
          font-size: 14px;
          margin: 2px 17px;
          line-height: 40px;
          display: block;
          &.capt-price {
            float: right;
            a {
              border-radius: 5px;
              border: 1px solid #fa7b0a;
              padding: 5px 15px;
              font-weight: bold;
              margin-right: 5px;
              color: $activ_col;
              position: relative;
              &:hover {
                text-decoration: none;
                font-size: 16px;
                &:before {
                  content: '\f105';
                  font-family: FontAwesome;
                  position: absolute;
                  color: $activ_col;
                  font-weight: bold;
                  left: 5px;
                  top: -6px; } } } } } } }
    .decr-item {
        position: relative;
        overflow: hidden;
        p {
          padding: 15px 0 8px 15px; }
        .descr-link {
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;
          height: 100%;
          opacity: 0;
          transform: translateY(30px);
          -webkit-transition: all .25s;
          -o-transition: all .25s;
          transition: all .25s;
          background-color: $activ_col;
          a {
            margin: 20px;
            padding: 3px 20px;
            float: left;
            display: block;
            text-align: center;
            border: 1px solid #fff;
            border-radius: 3px;
            color: #fff;
            &:last-child {
              float: right; }
            &:hover {
              text-decoration: none; } } }


        .decr-item:hover .descr-link {
          opacity: 1;
          transform: translateY(0); } } }
