    .decript-item:hover .decript-link {
      opacity: 1;
      transform: translateY(0); } } }
#button-more {
  cursor: pointer;
  width: 210px;
  height: 60px;
  margin: 0 auto;
  @include font("Opem Sans");
  font-size: 18px;
  background: url("images/glob.png") 0 0 no-repeat;
  position: relative;
  .arrL,
  .arrR {
    position: absolute; }

  .arrR {
    top: -6px;
    left: -2px;
    width: 60px;
    height: 68px;
    -webkit-animation: orbit2 4s linear infinite /* Chrome, Safari 5 */;
    -moz-animation: orbit2 4s linear infinite /* Firefox 5-15 */;
    -o-animation: orbit2 4s linear infinite /* Opera 12+ */;
    animation: orbit2 4s linear infinite /* Chrome, Firefox 16+, IE 10+, Safari 5 */;

    @-webkit-keyframes orbit2 {
      from {
        transform: rotate(0deg) translateX(0) rotate(-360deg); }
      to {
        transform: rotate(360deg) translateX(0) rotate(0deg); } }


    @-moz-keyframes orbit2 {
      from {
        transform: rotate(0deg) translateX(0) rotate(-360deg); }
      to {
        transform: rotate(360deg) translateX(0) rotate(0deg); } }


    @-o-keyframes orbit2 {
      from {
        transform: rotate(0deg) translateX(0) rotate(-360deg); }
      to {
        transform: rotate(360deg) translateX(0) rotate(0deg); } }

    @keyframes orbit2 {
      from {
        transform: rotate(0deg) translateX(0) rotate(-360deg); }
      to {
        transform: rotate(360deg) translateX(0) rotate(0deg); } } }
  .arrL {
    bottom: 27px;
    left: 0;
    height: 3px;
    width: 56px;
    -webkit-animation: orbit2 4s linear infinite /* Chrome, Safari 5 */;
    -moz-animation: orbit2 4s linear infinite /* Firefox 5-15 */;
    -o-animation: orbit2 4s linear infinite /* Opera 12+ */;
    animation: orbit2 4s linear infinite /* Chrome, Firefox 16+, IE 10+, Safari 5 */;

    @-webkit-keyframes orbit2 {
      from {
        transform: rotate(-360deg) translateY(0) rotate(0deg); }
      to {
        transform: rotate(-360deg) translateY(0) rotate(360deg); } }


    @-moz-keyframes orbit2 {
      from {
        transform: rotate(-360deg) translateY(0) rotate(0deg); }
      to {
        transform: rotate(-360deg) translateY(0) rotate(360deg); } }


    @-o-keyframes orbit2 {
      from {
        transform: rotate(-360deg) translateY(0) rotate(0deg); }
      to {
        transform: rotate(-360deg) translateY(0) rotate(360deg); } }

    @keyframes orbit2 {
      from {
        transform: rotate(-360deg) translate(0) rotate(0deg); }
      to {
        transform: rotate(-360deg) translate(0) rotate(360deg); } } }
  p {
    padding: 20px 0 0 0;
    float: right; } }
#cardTabs {
  border-top: 5px $activ_col solid;
  border-bottom: 1px solid #9a9a9a;
  .tabs {
    margin: 0;
    border-bottom: 3px $activ_col solid;
    .spantabs {
      width: 92%;
      margin: 0 auto;
      .hidespan {
        height: 35px;
        -webkit-box-shadow: 0 15px 8px -17px rgba(0, 0, 0, 0.5);
        -moz-box-shadow: 0 15px 8px -17px rgba(0, 0, 0, 0.5);
        box-shadow: 0 15px 8px -17px rgba(0, 0, 0, 0.5);
        .sandBtm {
          position: relative;
          height: 35px;
          width: 40px;
          float: right;
          margin-right: 10px; }

        $w: 27px;
        $h: 2px;
        #sandwich {
          width: $w;
          height: 15px;
          position: absolute;
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          margin: auto; }
        .sw-topper {
          position: relative;
          top: 0;
          width: $w;
          height: $h;
          background: $activ_col;
          border: none;
          border-radius: 4px 4px 4px 4px;
          transition: transform 0.5s, top 0.2s; }
        .sw-bottom {
          position: relative;
          width: $w;
          height: $h;
          top: $h * 2;
          background: $activ_col;
          border: none;
          border-radius: 4px 4px 4px 4px;
          transition: transform 0.5s, top 0.2s;
          transition-delay: 0.2s, 0s; }
        .sw-footer {
          position: relative;
          width: $w;
          height: $h;
          top: $h * 4;
          background: $activ_col;
          border: none;
          border-radius: 4px 4px 4px 4px;
          transition: all 0.5s;
          transition-delay: 0.1s; }
        #sandwich.active {
          .sw-topper {
            top: 10px;
            -webkit-transform: rotate(140deg);
            -moz-transform: rotate(140deg);
            -ms-transform: rotate(140deg);
            -o-transform: rotate(140deg);
            transform: rotate(140deg); }
          .sw-bottom {
            top: 5px;
            -webkit-transform: rotate(-140deg);
            -moz-transform: rotate(-140deg);
            -ms-transform: rotate(-140deg);
            -o-transform: rotate(-140deg);
            transform: rotate(-140deg); }
          .sw-footer {
            opacity: 0;
            top: 0;
            -webkit-transform: rotate(180deg);
            -moz-transform: rotate(180deg);
            -ms-transform: rotate(180deg);
            -o-transform: rotate(180deg);
            transform: rotate(180deg); } } } }
    .tab {
      display: block;
      float: left;
      text-align: center;
      position: relative;
      padding: 13px 28px;
      a {
        color: $font_col;
        text-transform: uppercase;
        text-decoration: none;
        @include font('Open Sans'); }
      &.active,
      &:hover {
        a {
          color: $activ_col;
          font-weight: bold;
          text-transform: uppercase; }
        &:after {
          content: '';
          position: absolute;
          border: 8px solid transparent;
          border-top: 8px solid $activ_col;
          left: 50%;
          margin-left: -12px;
          top: 0;
          width: 0; } } } }
  .tab-item {
    overflow: hidden;
    display: none;
    background-color: $cardtabs_col;
    &:first-child {
      display: block; }
    .topinfo {
      border-bottom: 1px solid #0f0f0f; }
    .infotur,
    .infoblock {
      width: 50%; }
    .infotur {
      float: left; }
    .info-items {
      border-bottom: 1px solid;
      &:last-child {
        border-bottom: none; }
      .info-name,
      .item-info {
        display: table-cell;
        padding: 4px 0;
        font-size: 18px; }
      .info-name {
        @include font("Roboto");
        font-weight: bold;
        width: 195px; }
      .item-info {
        @include font("Open Sans");
        width: 175px;
        .infoCal {
          border: 1px solid #9a9a9a;
          border-radius: 7px;
          width: 100%;
          position: relative;
          .fa {
            color: $activ_col;
            position: absolute;
            top: 20%;
            right: 7%;
            cursor: pointer; }
          input[type="text"] {
            width: 90%;
            border: none;
            background: $cardtabs_col;
            padding: 2px 4px;
            margin: 0 0 0 3px; } } }
      img {
        float: left;
        max-width: 656px;
        width: 100%; } }
    .infoblock {
      float: right;
      .priceblock {
        position: absolute;
        top: 140px;
        @include font("Open Sans");
        .price {
          color: $activ_col;
          font-size: 30px;
          text-align: center;
          border-bottom: 2px solid $activ_col;
          padding-bottom: 20px; }
        a {
          display: block;
          background-color: $btm_col;
          color: #ffffff;
          border-radius: 6px;
          padding: 8px;
          text-align: center;
          text-transform: uppercase;
          margin-top: 30px;
          &:hover {
            text-decoration: none; } } } } } }

